import React from "react";
import { Route, HashRouter } from "react-router-dom";
import Header from "./components/Header";
import Home from "./pages/Home";

function App() {
	return (
		<div className="App">
			<HashRouter>
				<Header />
				<div>
					<Route exact path="/" component={Home} />
				</div>
			</HashRouter>
		</div>
	);
}

export default App;
